.office {
	&__wrapper {
		display: flex;
		margin: 0 -15px;
		margin-top: 40px; }
	&__block {
		display: flex;
		flex-direction: column;
		margin: 0 15px;
		&-top {
			margin-bottom: 30px; }
		&-title {
			font-size: 18px;
			font-weight: 600;
			margin-bottom: 7px; }
		&-text {
			line-height: 30px; } }
	&__img {
		margin-top: auto;
		max-width: 100%;
		border-radius: 20px; } }

.map {
	&__title {
		margin-top: 50px;
		margin-bottom: 30px; }
	&__map {
		padding-top: 20px;
		padding-bottom: 40px; } }

@media (max-width: 900px) {
	.map {
		&__title,
		&__map {
			display: none; } } }

@media (max-width: 600px) {
	.office {
		&__wrapper {
			flex-direction: column;
			margin: 0; }
		&__block {
			flex-direction: column-reverse;
			margin: 0;
			margin-top: 20px;
			&-top {
				margin-top: 15px;
				margin-bottom: 0; } } } }
