.masters {
	&__list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin: -15px;
		margin-top: 25px; }
	&__item {
		display: flex;
		flex-direction: column;
		width: calc(25% - 30px);
		margin: 15px; }
	&__img {
		border-radius: 10px;
		max-width: 100%; }
	&__name {
		font-size: 22px;
		font-weight: 600;
		color: #181818;
		margin-top: 25px; }
	&__text {
		font-size: 16px;
		margin-top: 10px;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0; } }
	&__btn {
		display: block;
		margin: auto;
		margin-top: 40px;
		min-width: 240px;
		&-order {
			margin-top: auto;
			display: block; } }
	&__carousel {
		display: none !important; } }

@media (max-width: 900px) {
	.masters {
		&__item {
			max-width: 285px;
			width: calc(50% - 30px); }
		&__name {
			font-size: 18px; } } }

@media (max-width: 746px) {
	.masters {
		&__list,
		&__btn {
			display: none; }
		&__carousel {
			display: block !important;
			margin-top: 20px;
			& .owl-dots {
				margin-top: 30px !important; } }
		&__item {
			width: 100%;
			margin: auto; } } }
