.question-article {
	overflow: hidden;
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__block {
		width: calc(50% - 15px);
		max-width: 585px;
		padding-right: 20px; }
	&__img {
		margin-bottom: 10px; }
	&__text {
		font-size: 16px;
		color: #232323;
		line-height: 30px;
		margin-top: 20px; }
	&__form {
		position: relative;
		display: flex;
		align-items: center;
		width: calc(50% - 15px);
		max-width: 600px;
		background-color: #f5f5f5;
		padding: 40px 0;
		padding-left: 30px;
		border-radius: 20px 0 0 20px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 100%;
			width: 100vw;
			height: 100%;
			background-color: #f5f5f5; } }
	&__input {
		width: 100%;
		background-color: #fff;
		border-radius: 10px 0 0 10px;
		padding: 15px 25px;
		border: none;
		font-size: 16px;
		color: #232323; }
	&__btn {
		border-radius: 0 10px 10px 0; } }

@media (max-width: 900px) {
	.question-article {
		&__wrap {
			flex-direction: column;
			align-items: flex-start; }
		&__block {
			max-width: none;
			padding-right: 0;
			width: auto; }
		&__form {
			width: auto;
			flex-direction: column;
			align-items: flex-start;
			background-color: transparent;
			margin-top: 20px;
			padding: 0;
			&:before {
				display: none; } }
		&__input {
			border: 1px solid #f5f5f5;
			border-radius: 10px;
			min-width: 290px;
			margin-bottom: 20px; }
		&__btn {
			min-width: 290px;
			border-radius: 10px; } } }
