.question {
	&__block {
		margin-top: 40px;
		display: flex;
		flex-direction: column; }
	&__label {
		padding: 25px 30px;
		background-color: #fff;
		border-radius: 10px;
		margin-bottom: 15px;
		cursor: pointer;
		&:hover {
			background-color: #d22a2a;
			color: #fff;
			.question__question span {
				background-color: #d22a2a;
				background-image: url(../img/arrow/arrow-down-white.png); }
			input:checked ~ .question__question span {
				background-image: url(../img/arrow/arrow-down-white.png);
				transform: rotate(180deg); } }
		input:checked ~ .question__answer {
			display: block; }
		input:checked ~ .question__question span {
			background-image: url(../img/arrow/arrow-up-grey.png); } }
	&__question {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 18px;
		font-weight: 500;
		span {
			min-width: 14px;
			min-height: 14px;
			background: url(../img/arrow/arrow-down-black.png) no-repeat center;
			margin-left: 10px; } }
	&__answer {
		display: none;
		border-top: 1px solid #e5e5e5;
		margin-top: 25px;
		padding-top: 20px;
		line-height: 30px; }
	&__btn {
		display: none; } }

@media (max-width: 900px) {
	.question {
		&__block {
			margin-top: 20px; }
		&__label {
			padding: 15px 10px;
			position: relative;
			margin-bottom: 33px;
			text-align: center;
			&:hover {
				input:checked ~ .question__question span {
					background-position: center 11px; } }
			&:nth-child(n+6) {
				display: none; } }
		&__question {
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-size: 18px;
			font-weight: 500;
			span {
				position: absolute;
				bottom: -23px;
				left: calc(50% - 23px);
				width: 42px;
				height: 42px;
				background-color: #fff;
				background-position: center 25px;
				border-radius: 50%;
				margin: 0; } }
		&__answer {
			font-size: 15px;
			line-height: 24px;
			border-top: none;
			padding-top: 0;
			margin-top: 20px; }
		&__btn {
			display: block;
			min-width: 290px;
			margin: auto;
			margin-top: 20px; } } }
