.block-img-right {
	margin-bottom: 80px;
	overflow: hidden;
	&--app {
		background: #f5f5f5 url(../img/uslugi-cen/bg.jpg) no-repeat center bottom; }
	&__wrap {
		display: flex;
		align-items: flex-start; }
	&__block {
		min-width: 60%;
		width: 60%;
		&-text {
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } } }
	&__text {
		display: none;
		font-size: 16px;
		line-height: 30px;
		margin-top: 15px;
		&:nth-child(-n+2) {
			display: block; } }
	&__app {
		font-size: 22px;
		font-weight: 600;
		margin-top: 60px;
		&-block {
			display: flex;
			align-items: center;
			margin-top: 20px; }
		&-link {
			margin-right: 30px;
			&:last-child {
				margin-right: 0 !important; }
			img {
				max-width: 100%; } } }
	&__link {
		display: block;
		font-size: 16px;
		font-weight: 600;
		color: #d22a2a;
		margin-top: 15px;
		&:hover {
			color: #000; }
		&--active {
			display: none; } }
	&__block--active .block-img-right__text {
		display: block !important; }
	&__img {
		margin-left: 40px; }
	&__list {
		margin-top: 20px;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-wrap: wrap;
		&--model li {
			max-width: none; } }
	&__item {
		max-width: 380px;
		display: flex;
		align-items: flex-start;
		margin-top: 20px;
		padding-right: 10px;
		&-img {
			margin-right: 20px; }
		&-text,
		&-text-small {
			line-height: 1.5;
			color: #232323; }
		&-text {
			font-size: 18px;
			font-weight: 600;
			&-small {
				font-size: 16px;
				margin-top: 10px; } } }
	&__link {
		&-list {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin: 30px 0; }
		&-item {
			margin: 5px 0;
			margin-right: 38px;
			cursor: pointer;
			&:last-child {
				margin-right: 0; }
			span {
				font-size: 16px;
				font-weight: 500;
				color: #d22a2a;
				border-bottom: 1px dashed #d22a2a;
				&:hover {
					color: #181818;
					border-color: #181818; } }
			input:checked ~ span {
				color: #181818;
				border-color: #181818; } } }
	&__select {
		display: none; }
	&__certificate {
		margin-top: 40px;
		display: flex;
		&-block {
			min-width: 400px;
			width: 50%; } }
	&__carousel {
		display: flex;
		width: 50%;
		.owl-dots {
			display: none; } } }


@media (max-width: 900px) {
	.block-img-right {
		margin-bottom: 40px;
		&--app {
			background: #f5f5f5; }
		&__block {
			width: auto;
			min-width: auto; }
		&__title {
			margin-bottom: 20px; }
		&__text {
			margin-top: 15px;
			&:nth-child(3) {
				display: none; }
			&:first-child {
				display: block; } }
		&__link {
			margin-top: 15px; }
		&__img {
			display: none; }
		&__app {
			display: none;
			&-block {
				margin-top: 20px; }
			&-link {
				margin-right: 15px;
				img {
					width: 140px; } } }
		&__list {
			margin-top: 0;
			&--model {
				p {
					display: block; } } }
		&__item {
			align-items: center;
			max-width: none;
			&-img {
				width: 64px; }
			&-text {
				font-size: 16px;
				&-small {
					display: none; } } }
		&__certificate {
			flex-direction: column;
			&-block {
				width: auto;
				min-width: auto; } }
		&__carousel {
			width: auto;
			margin-top: 20px;
			.owl-dots {
				display: block; } } } }

@media (max-width: 600px) {
	.block-img-right {
		&__link-list {
			display: none; }
		&__select {
			display: block;
			margin: 20px 0;
			min-width: 290px;
			padding: 18px 20px;
			border: 1px solid #f5f5f5;
			border-radius: 10px;
			background-color: transparent;
			font-size: 16px;
			color: #232323; }
		&__list--model img {
			display: none; } } }
