.footer {
	padding: 40px 0;
	background-color: #f5f5f5;
	&__wrap {
		display: flex;
		justify-content: space-between; }
	&__block-menu {
		display: flex;
		justify-content: space-between;
		flex-grow: 2; }
	&__menu-mobile {
		display: none; }
	&__contacts {
		text-align: right;
		padding-left: 40px;
		margin-left: 40px;
		border-left: 1px solid #dbdbdb;
		font-size: 16px;
		line-height: 30px;
		color: #181818;
		& > div {
			border-bottom: 1px solid #dbdbdb;
			padding-bottom: 30px;
			margin-bottom: 30px;
			&:last-child {
				border: none;
				margin-bottom: 0; } }
		&-tel {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			div {
				margin-right: 20px; }
			a {
				font-size: 20px;
				font-weight: 700;
				color: #181818; } }
		&-social {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			a {
				display: flex;
				align-items: center;
				margin: 0 10px;
				&:hover {
					opacity: .8; } } } }
	&__menu {
		&-list {}
		&-item {
			margin-bottom: 12px;
			&:first-child {
				margin-bottom: 20px; }
			&:last-child {
				margin-bottom: 0; } }
		&-title {
			font-weight: 600;
			color: #181818; }
		&-link {
			color: #000;
			&:hover {
				color: #d22a2a; } } }
	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 20px 0;
		font-size: 16px;
		color: #181818;
		a {
			margin-left: 20px;
			color: #181818;
			&:hover {
				color: #d22a2a; } } } }

@media (max-width: 1180px) {
	.footer__menu-list:nth-child(n+3) {
		display: none; } }

@media (max-width: 1000px) {
	.footer__menu-list:nth-child(n+2) {
		display: none; } }

@media (max-width: 750px) {
	.footer {
		padding: 30px 0;
		&__block-menu {
			display: none; }
		&__contacts {
			text-align: left;
			padding-left: 0;
			margin-left: 0;
			border-left: none;
			width: 100%;
			& > div {
				border-bottom: none;
				padding-bottom: 0;
				margin-bottom: 15px; }
			&-tel {
				justify-content: flex-start;
				flex-wrap: wrap;
				a {
					min-width: 290px;
					margin: 5px 0; } }
			&-social {
				justify-content: flex-start; } }
		&__menu-mobile {
			display: block;
			&-label {
				display: flex;
				flex-direction: column;
				font-weight: 600;
				padding: 15px 0;
				border-bottom: 1px solid #dbdbdb;
				width: 100%;
				&:first-child {
					border-top: 1px solid #dbdbdb; }
				input:checked ~ .footer__menu-mobile-list {
					display: flex; } }
			&-list {
				display: none;
				flex-direction: column;
				font-weight: 400;
				margin-top: 10px; } } } }

@media (max-width: 700px) {
	.footer__bottom {
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
		div {
			display: flex;
			flex-direction: column; }
		a {
			margin-left: 0px;
			margin-top: 7px; } } }

