.breadcrumbs {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 35px;
	margin-bottom: 35px;
	&__item {
		position: relative;
		margin: 5px 30px 5px 0;
		a {
			text-decoration: underline;
			&:hover {
				color: #d22a2a; } }
		&:last-child {
			margin-right: 0; }
		&::before {
			content: '•';
			position: absolute;
			top: 0;
			left: -15px;
			color: #4c4c4c; }
		&:first-child::before {
			display: none; } }
	&__link {
		font-size: 15px;
		color: #4c4c4c; } }

@media (max-width: 900px) {
	.breadcrumbs {
		margin-top: 20px;
		margin-bottom: 20px; } }
