.city-work {
	&__block {
		display: flex;
		flex-wrap: wrap;
		margin: -4px;
		margin-top: 20px; }
	&__column {
		width: 168px;
		padding-right: 10px;
		margin: 4px;
		margin-top: 20px; }
	&__label {
		font-size: 18px;
		font-weight: 600;
		color: #232323; }
	&__list {
		margin-top: 20px; }
	&__item {
		margin-bottom: 4px; }
	&__link {
		font-size: 15px;
		color: #232323;
		&:hover {
			color: #d22a2a; } } }

@media (max-width: 590px) {
	.city-work {
		&__block {
			flex-direction: column;
			margin: 0;
			margin-top: 20px; }
		&__column {
			padding: 0;
			margin: 0;
			width: auto;
			input:checked ~ .city-work__list {
				display: block; }
			input:checked ~ .city-work__label {
				background-image: url(../img/arrow/arrow-up-grey.png); } }
		&__label {
			display: block;
			background: #f5f5f5 url(../img/arrow/arrow-down-black.png) no-repeat calc(100% - 20px) center;
			border-radius: 10px;
			padding: 20px;
			cursor: pointer;
			margin-top: 10px; }
		&__list {
			display: none;
			margin: 10px 20px; } } }
