.stock {
	&__block {
		margin-top: 45px;
		padding: 120px 77px;
		background: url(../img/index/article.png) no-repeat center;
		background-size: cover;
		border-radius: 10px; }
	&__title {
		max-width: 460px;
		margin-bottom: 30px;
		font-size: 30px;
		font-weight: 600;
		color: #fff;
		line-height: 1.4;
		span {
			color: #fde780; } }
	&__btn {
		min-width: 285px; }
	&__carousel .owl-dots {
		display: none; } }

@media (max-width: 900px) {
	.stock {
		margin-bottom: 0;
		&__wrap {
			margin: 0; }
		&__block {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
			margin-top: 25px;
			padding: 40px 15px;
			padding-bottom: 100px;
			border-radius: 0px; }
		&__title {
			max-width: none;
			font-size: 22px; }
		&__carousel .owl-dots {
			display: block;
			position: absolute;
			bottom: 0;
			width: 100%;
			margin-bottom: 40px; }
		& .owl-dots .owl-dot span {
			background-color: #fff; } } }
