.fast-order {
	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 25px;
		margin-bottom: 50px; }
	&__order {
		position: relative;
		width: 49%;
		max-width: 600px;
		height: 200px;
		margin-top: 20px;
		background-color: #fff;
		border-radius: 10px;
		color: #232323;
		&:hover {
			background-color: #e6e6e6; }
		&--pc {
			background: #fff url(../img/index/img-card-2.png) no-repeat center; }
		&--book {
			background: #fff url(../img/index/img-card-1.png) no-repeat center; }
		&-title {
			position: absolute;
			top: 55px;
			left: 0;
			min-width: 180px;
			background-color: #ffe749;
			border-radius: 0 10px 10px 0;
			padding: 15px 28px;
			font-size: 20px;
			font-weight: 600; }
		&-price {
			position: absolute;
			bottom: 55px;
			left: 28px;
			font-size: 18px;
			font-weight: 500; } } }

@media (max-width: 900px) {
	.fast-order {
		&__wrapper {
			flex-direction: column;
			align-items: flex-start;
			margin-top: 0;
			margin-bottom: 50px; }
		&__order {
			width: 100%;
			height: 145px;
			&-title {
				top: 33px;
				min-width: 148px;
				padding: 12px 18px;
				font-size: 18px; }
			&-price {
				bottom: 33px;
				left: 18px; } } } }
