.akcii-skidk {
	&__wrap {
		display: flex;
		flex-direction: column; }
	&__wrapper {
		display: flex;
		margin: 0 -15px;
		&:nth-child(odd) {
			flex-direction: row-reverse; } }
	&__block {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: red;
		margin: 15px;
		border-radius: 10px;
		overflow: hidden;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 100%;
			background: linear-gradient(rgba(255,255,255,0), rgba(0,0,0,0.7));
			opacity: 0.5; }
		&:hover::before {
			background: linear-gradient(rgba(255,255,255,0), rgba(0,0,0,1)); } }
	&__img {
		max-width: 100%; }
	&__text {
		position: absolute;
		bottom: 15px;
		left: 25px;
		max-width: calc(100% - 50px);
		font-size: 22px;
		font-weight: 600;
		color: #fff;
		line-height: 1.6;
		span {
			font-weight: 700;
			color: #ffe749; } } }



@media (max-width: 1260px) {
	.akcii-skidk__text {
		font-size: calc(0.4rem + 1.20vw); } }

@media (max-width: 900px) {
	.akcii-skidk {
		&__wrapper {
			flex-wrap: wrap;
			justify-content: center; }
		&__block {
			width: 290px; }
		&__img {
			max-width: none;
			max-height: 335px; }
		&__text {
			left: 18px;
			max-width: calc(100% - 36px);
			font-size: 16px; } } }
