.work-process {
	&__title {
		display: flex;
		align-items: center;
		font-size: 22px;
		font-weight: 600;
		color: #181818;
		span {
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			width: 46px;
			height: 46px;
			background-color: #fff;
			border-radius: 10px;
			margin-right: 18px;
			font-size: 20px;
			color: #565656; } }
	&__text {
		margin-top: 20px;
		font-size: 16px;
		line-height: 30px; }
	&__carousel {
		margin-top: 45px;
		& .owl-dots {
			margin-top: 30px !important;
			text-align: left; } } }

@media (max-width: 900px) {
	.work-process {
		&__title {
			font-size: 16px;
			span {} }
		&__carousel {
			margin-top: 25px;
			& .owl-dots {
				margin-top: 30px !important;
				text-align: center; } } } }
