.pagination {
	display: flex;
	&__item {
		display: none;
		margin-right: 10px;
		border-radius: 10px;
		overflow: hidden;
		&:first-child,
		&:nth-child(2),
		&:nth-child(3),
		&:nth-child(4),
		&:nth-child(5),
		&:nth-child(6),
		&:last-child {
			display: block; } }
	&__link {
		background-color: #ffe749;
		width: 50px;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		font-size: 16px;
		font-weight: 700;
		color: #000;
		&--prev,
		&--next {
			width: 90px; }
		&--active {
			background-color: #676767 !important;
			color: #fff; }
		&:hover {
			background-color: #d22a2a;
			color: #fff; } } }

@media (max-width: 600px) {
	.pagination {
		&__item {
			display: none;
			&:first-child,
			&:nth-child(2),
			&:nth-child(3),
			&:nth-child(4),
			&:last-child {
				display: block; } }
		&__link {
			position: relative;
			&--prev,
			&--next {
				display: block;
				width: 50px;
				font-size: 0; }
			&--prev::before,
			&--next::before {
				position: absolute;
				display: flex;
				align-items: center;
				justify-content: center;
				top: calc(50% - 17px);
				left: calc(50% - 15px);
				width: 30px;
				height: 30px;
				font-size: 30px;
				line-height: 30px; }
			&--prev::before {
				content: '«‎'; }
			&--next::before {
				content: '»‎'; } } } }
