.problem {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -15px;
		margin-top: 30px;
		li {
			margin: 15px; } }
	&__link {
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: #fff;
		border-radius: 10px;
		padding: 0 20px;
		padding-top: 47px;
		padding-bottom: 22px;
		width: 285px;
		height: 100%;
		text-align: center;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.8;
		color: #232323;
		&:hover {
			background-color: #e6e6e6; } }
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 80px;
		margin-bottom: 10px; }
	&__title {
		font-size: 18px;
		font-weight: 500;
		color: #232323; }
	&__text {
		display: flex;
		justify-content: center;
		align-items: center;
		min-height: 42px; } }

@media (max-width: 646px) {
	.problem {
		&__list {
			margin: -5px;
			margin-top: 20px;
			li {
				display: flex;
				width: 100%;
				margin: 0; } }
		&__link {
			width: 100%;
			height: auto;
			min-height: 100px;
			padding: 25px 20px;
			flex-direction: row;
			align-items: flex-start;
			justify-content: flex-start;
			margin: 5px;
			text-align: left; }
		&__img {
			width: 50px;
			height: 50px;
			margin-right: 20px;
			img {
				max-width: 50px;
				max-height: 50px; } }
		&__block-text {
			display: flex;
			flex-direction: column; }
		&__titile {
			font-size: 16px; }
		&__text {
			font-size: 15px; } } }
