.title-block {
	margin-top: 60px;
	margin-bottom: 55px;
	&--top {
		margin-top: 0; }
	&--main {
		margin-top: 120px;
		margin-bottom: 258px; }
	&__wrap {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }
	&__block {
		position: relative;
		z-index: 10;
		max-width: 600px; }
	&__title {
		margin-bottom: 25px; }
	&__sub-title {
		font-size: 20px;
		span {
			font-weight: 600; } }
	&__text {
		font-size: 16px;
		line-height: 1.5;
		margin-bottom: 37px; }
	&__btn {
		min-width: 285px;
		padding-left: 10px;
		padding-right: 10px;
		&-wrap {
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-wrap: wrap;
			margin-top: 40px;
			a {
				margin-top: 10px;
				&:first-child {
					margin-right: 30px; } } } }
	&__carousel {
		position: absolute;
		top: -60px;
		right: 0;
		height: 550px;
		width: 65%;
		div {
			display: flex;
			margin-left: auto; }
		& .owl-dots {
			margin-top: 0 !important;
			position: absolute;
			right: 0;
			bottom: 100px;
			width: 1230px; } }
	&__img {
		margin-top: -20px;
		max-width: 100%;
		max-width: 50%;
		&--mobile {
			display: none; } }
	&__list {
		display: flex;
		justify-content: space-between;
		margin-top: 40px; }
	&__item {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		img {
			margin-right: 10px; }
		&-text {
			font-size: 20px;
			font-weight: 600; } } }


@media (max-width: 1260px) {
	.title-block__carousel .owl-dots {
		width: calc(100vw - 45px); } }

@media (max-width: 900px) {
	.title-block {
		margin-top: 30px;
		margin-bottom: 50px;
		&--main {
			margin-bottom: 100px; }
		&__block {
			padding-top: 0; }
		&__title {
			font-size: 26px;
			margin-bottom: 20px; }
		&__text {
			margin-bottom: 20px; }
		&__carousel {
			display: none !important; }
		&__img {
			display: none;
			&--mobile {
				display: block;
				margin-top: 0px;
				max-width: 100%;
				margin-bottom: 20px; } }
		&__btn-wrap {
			margin-top: 20px; }
		&__list {
			margin-top: 20px; } } }

@media (max-width: 900px) {
	.title-block {
		&__list {
			flex-direction: column; }
		&__item {
			margin-top: 10px;
			align-items: center;
			div {
				display: flex;
				align-items: center; }
			p {
				line-height: 20px; }
			&-text {
				margin-left: 7px; } } } }
