.stats {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -15px;
		margin-top: 25px; }
	&__item {
		width: 285px;
		padding: 25px 30px;
		margin: 15px;
		border-radius: 10px;
		background-color: #f5f5f5; }
	&__stat {
		font-size: 42px;
		font-weight: 500;
		color: #d22a2a; }
	&__text {
		line-height: 24px; } }

@media (max-width: 900px) {
	.stats {
		&__list {
			margin: -5px;
			margin-top: 15px; }
		&__item {
			padding: 10px 30px;
			margin: 5px;
			border-radius: 10px;
			background-color: #f5f5f5; }
		&__stat {
			font-size: 42px;
			font-weight: 500;
			color: #d22a2a; }
		&__text {
			line-height: 24px; } } }

@media (max-width: 609px) {
	.stats__item {
		width: 100%; } }
