.info-block {
	overflow: hidden;
	&__wrap {
		position: relative;
		height: 600px; }
	&__block {
		position: relative;
		padding-top: 40px;
		width: 575px;
		z-index: 10;
		h2 {
			margin-top: 55px; }
		&--right {
			margin-left: auto; } }
	&__subtitle {
		font-size: 26px;
		font-weight: 600;
		color: #181818;
		margin-top: 20px; }
	&__btn {
		margin-top: 20px; }
	&__text {
		margin-top: 10px;
		line-height: 25px; }
	&__carousel {
		position: absolute;
		top: 0;
		left: 630px;
		height: 600px;
		width: 945px;
		div {
			display: flex; }
		& .owl-dots {
			margin-top: 0 !important;
			position: absolute;
			right: 0;
			bottom: 40px;
			width: calc(100% + 630px); }
		&--left {
			left: auto;
			right: 630px;
			& .owl-dots {
				right: auto;
				left: 0;
				width: calc(100% + 215px);
				justify-content: flex-end; } } } }

@media (max-width: 900px) {
	.info-block {
		margin-top: 0 !important;
		margin-bottom: 0 !important;
		&__block {
			width: auto;
			color: #fff;
			text-align: center;
			h2 {
				color: #fff;
				margin-top: 0; } }
		&__subtitle {
			color: #fff;
			font-size: 22px; }
		&__logo {
			display: none; }
		&__carousel {
			position: absolute;
			top: 0;
			left: -30px;
			height: 100%;
			&-block::before {
				content: '';
				position: absolute;
				width: 100%;
				height: 100%;
				background-color: rgba(0,0,0,.3); }
			& .owl-dots {
				right: auto;
				justify-content: center;
				width: calc(100vw + 30px); }
			& .owl-dots .owl-dot span {
				background-color: #fff; } } } }
