.article-open {
	&__top {
		margin-top: 40px;
		margin-bottom: 40px; }
	&__img {
		float: left;
		width: 67%;
		max-width: 100%;
		margin-right: 20px;
		border-radius: 10px;
		&-text {
			font-size: 16px;
			line-height: 1.8;
			font-weight: 500; } }
	&__block-text {
		border-bottom: 1px solid #e7e7e7;
		padding-bottom: 10px;
		margin-top: 24px;
		font-size: 16px;
		line-height: 1.8;
		h2 {
			margin-top: 40px;
			margin-bottom: 30px;
			font-size: 32px;
			font-weight: 600;
			color: #181818; }
		p {
			margin-bottom: 20px; }
		a {
			color: #d22a2a;
			font-weight: 500; }
		img {
			max-width: 100%;
			margin-bottom: 20px;
			border-radius: 10px; }
		ul,
		ol {
			padding-left: 20px;
			margin-bottom: 20px; }
		ul {
			list-style: disc; }
		ol {
			list-style: decimal; } }
	&__info {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-top: 20px;
		&-data {
			display: flex;
			align-items: center;
			margin: 15px 0;
			p {
				margin-right: 30px; } }
		&-social {
			max-width: 810px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			flex-wrap: wrap;
			margin: 0 -5px;
			flex-grow: 2;
			a {
				display: flex;
				margin: 5px 5px;
				img {
					max-width: 100%; } } } } }

@media (max-width: 900px) {
	.article-open {
		&__top {
			margin-top: 20px;
			margin-bottom: 20px; }
		&__img {
			display: block;
			width: 100%;
			margin-right: 0;
			margin-bottom: 20px; }
		&__block-text {
			padding-bottom: 0;
			margin-top: 20px;
			h2 {
				margin-top: 20px;
				margin-bottom: 10px;
				font-size: 22px; }
			p {
				margin-bottom: 10px; }
			img {
				margin-bottom: 10px; }
			ul,
			ol {
				margin-bottom: 10px; } }
		&__info {
			&-data {
				justify-content: space-between;
				width: 100%;
				margin: 0;
				p {
					margin-right: 10px;
					&:last-child {
						margin-right: 0; } } }
			&-social {
				margin-top: 15px;
				width: 100%;
				max-width: 900px;
				justify-content: flex-start;
				a {
					width: 140px; } } } } }
