.brand {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
		margin-top: 15px; }
	&__item {
		width: calc(25% - 30px);
		margin: 15px;
		&:hover {
			.brand__text {
				color: #d22a2a; } } }
	&__img {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		border-radius: 10px;
		height: 220px;
		padding: 0 20px;
		img {
			max-width: 100%; } }
	&__text {
		margin-top: 15px;
		font-size: 16px;
		font-weight: 600;
		color: #232323; } }

@media (max-width: 900px) {
	.brand {
		&__item {
			width: calc(33% - 30px); } } }

@media (max-width: 700px) {
	.brand {
		&__item {
			width: calc(50% - 30px); } } }

@media (max-width: 500px) {
	.brand {
		&__list {
			flex-direction: column;
			margin: 0;
			margin-top: 10px; }
		&__item {
			width: 100%;
			margin: 0;
			margin-top: 10px; }
		&__link {
			display: flex;
			align-items: center;
			background-color: #fff;
			padding: 10px 20px; }
		&__img {
			min-width: 70px;
			width: 70px;
			height: auto;
			padding: 0;
			margin-right: 20px; }
		&__text {
			margin-top: 0; } } }
