.uslugi-cen {
	&__text {
		margin-top: 20px;
		font-size: 16px;
		line-height: 1.8; }
	&__uslugi {
		background-color: #f5f5f5;
		padding: 40px 0;
		margin-top: 40px; }
	&__form {
		display: flex;
		align-items: center;
		width: 100%;
		border-radius: 20px 0 0 20px;
		margin-top: 20px; }
	&__input {
		width: 100%;
		background-color: #fff;
		border-radius: 10px 0 0 10px;
		padding: 15px 25px;
		border: none;
		font-size: 16px;
		color: #232323; }
	&__btn {
		min-width: 180px;
		border-radius: 0 10px 10px 0;
		background: #ffe749 url(../img/uslugi-cen/filter.png) no-repeat 30px center;
		padding-left: 50px; }
	&__city-mobil {
		display: none; } }

@media (max-width: 900px) {
	.uslugi-cen {
		&__uslugi {
			padding: 20px 0;
			margin-top: 20px; }
		&__form {
			width: auto;
			flex-direction: column;
			align-items: flex-start;
			background-color: transparent;
			margin-top: 20px;
			padding: 0;
			&:before {
				display: none; } }
		&__input {
			border: 1px solid #f5f5f5;
			border-radius: 10px;
			min-width: 290px;
			margin-bottom: 20px; }
		&__btn {
			min-width: 290px;
			border-radius: 10px;
			background-position: 85px center; }
		&__city-mobil {
			display: block; }
		&__city-desc {
			display: none; } } }
