.comment {
	&__carousel {
		display: none !important; }
	&__block {
		display: flex;
		align-items: flex-start;
		margin: 0 -15px;
		margin-top: 25px; }
	&__column {
		margin: 15px;
		width: calc(33.3% - 15px); }
	&__comment {
		padding: 25px 24px;
		border-radius: 10px;
		background-color: #f8f8f8;
		margin-bottom: 30px;
		color: #232323;
		line-height: 1.8;
		&:last-child {
			margin-bottom: 0; } }
	&__top {
		display: flex;
		align-items: flex-start;
		justify-content: space-between; }
	&__name {
		font-weight: 500;
		line-height: 100%;
		margin-right: 10px; }
	&__star {
		display: flex;
		align-items: center;
		justify-content: space-between;
		min-width: 100px; }
	&__title {
		font-size: 18px;
		font-weight: 600;
		margin-top: 20px; }
	&__text {
		margin-top: 10px; } }

@media (max-width: 900px) {
	.comment {
		&__block {
			display: none; }
		&__carousel {
			display: block !important;
			margin-top: 20px; } } }
