.brand-category {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -15px;
		margin-top: 20px;
		&--repairs {
			.brand-category__item {
				min-height: 160px; } } }
	&__item {
		display: flex;
		background-color: #fff;
		border-radius: 10px;
		margin: 15px;
		width: calc(25% - 30px);
		&--more > a {
			align-items: center;
			justify-content: center;
			padding: 30px; } }
	&__link {
		display: flex;
		flex-direction: column;
		width: 100%;
		padding: 30px;
		padding-bottom: 20px;
		font-size: 16px;
		color: #232323;
		&:hover .brand-category__link-title {
			color: #d22a2a; }
		&-brand {
			margin-bottom: 5px; }
		&-title {
			font-weight: 600; }
		&-clock {
			color: #6f6f6f;
			margin: 8px 0;
			img {
				margin-right: 10px; } } }
	&__price {
		margin-top: auto;
		span {
			font-size: 18px;
			font-weight: 600; } }
	&__btn {
		display: none; } }

@media (max-width: 1000px) {
	.brand-category {
		&__item {
			width: calc(33.3% - 30px); } } }

@media (max-width: 700px) {
	.brand-category {
		&__list {
			margin: -5px;
			margin-top: 20px;
			&--repairs {
				.brand-category__item {
					min-height: auto; } } }
		&__item {
			margin: 5px;
			width: calc(50% - 10px);
			&--more > a {
				padding: 20px 30px; } }
		&__link {
			padding: 20px 30px;
			padding-bottom: 12px; } } }

@media (max-width: 500px) {
	.brand-category {
		&__list {
			margin: 0;
			margin-top: 20px; }
		&__item {
			margin: 5px 0;
			width: 100%;
			&--more {
				display: none; } }
		&__btn {
			display: block;
			width: 100%;
			margin-top: 15px; } } }
