.contacts-form {
	&__form {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		padding: 30px;
		background-color: #f5f5f5;
		border-radius: 20px;
		margin-top: 40px; }
	&__wrapper {
		display: flex;
		justify-content: space-between;
		width: 100%; }
	&__input,
	&__textarea {
		background-color: #fff;
		border-radius: 10px;
		padding: 15px 25px;
		border: none;
		font-size: 16px;
		color: #232323; }
	&__input {
		width: calc(33% - 15px);
		margin-bottom: 20px; }
	&__textarea {
		resize: none;
		min-height: 178px;
		width: 100%;
		margin-bottom: 30px; }
	&__btn {
		min-width: 290px; } }

@media (max-width: 900px) {
	.contacts-form {
		margin: 0;
		padding: 40px 0;
		background-color: #f5f5f5;
		&__form {
			margin-top: 20px;
			padding: 0; } } }

@media (max-width: 740px) {
	.contacts-form {
		&__wrapper {
			justify-content: flex-start;
			flex-wrap: wrap; }
		&__input {
			width: 100%;
			margin-bottom: 10px; }
		&__textarea {
			margin-bottom: 20px;
			min-height: 150px; } } }
