.social {
	&__block {
		display: flex;
		justify-content: space-between;
		margin-top: 20px; }
	&__link {
		max-width: 600px;
		width: 49%;
		margin-top: 20px;
		img {
			max-width: 100%; } } }

@media (max-width: 900px) {
	.social {
		&__block {
			flex-wrap: wrap;
			margin: -10px;
			margin-top: 10px; }
		&__link {
			width: auto;
			margin: 10px; } } }
