.cabinet {
	&__wrap {
		padding: 55px 0;
		&--grey {
			background-color: #f5f5f5; } }
	&__btn {
		border: none;
		background-color: transparent;
		display: block;
		font-size: 16px;
		color: #d22a2a;
		cursor: pointer; }
	&__main {
		display: flex;
		justify-content: space-between;
		margin-top: 40px;
		&-block {
			display: flex;
			align-items: flex-start; }
		&-avatar {
			margin-right: 30px;
			max-width: 100%;
			width: 179px;
			height: 179px;
			border-radius: 10px; }
		&-welcome {
			font-weight: 600; }
		&-discounts {
			margin-top: 10px;
			&--mobile {
				display: none; } }
		&-column {
			display: flex;
			flex-direction: column;
			p {
				font-size: 16px;
				color: #181818;
				margin-bottom: 7px;
				span {
					color: #d22a2a;
					font-weight: 600; } } }
		&-data {
			display: flex;
			justify-content: flex-start;
			align-items: flex-end;
			margin-bottom: 10px;
			p {
				display: flex;
				flex-wrap: wrap;
				flex-shrink: 2;
				margin-bottom: 0;
				margin-right: 20px;
				span {
					color: #181818; } } }
		&-btn {
			margin-top: 10px;
			font-weight: 600; } }
	&__card {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-top: 40px;
		&-card {
			margin: 10px 0;
			margin-right: 30px;
			font-size: 18px;
			font-weight: 600;
			color: #181818;
			span {
				margin-left: 10px; } }
		&-btn {
			display: flex;
			align-items: center;
			button {
				margin-right: 20px; } } }
	&__master {
		&-block {
			display: flex;
			margin-top: 40px; }
		&-img {
			width: 290px;
			border-radius: 10px;
			margin-right: 30px; }
		&-info {}
		&-name {
			font-size: 22px;
			font-weight: 600;
			color: #181818;
			margin-bottom: 10px; }
		&-text {
			line-height: 1.8;
			margin-bottom: 7px; }
		&-btn {
			margin-top: 20px; } }
	&__chat {
		&-chat {
			padding: 30px 35px;
			background-color: #fff;
			border-radius: 10px;
			margin-top: 40px;
			max-height: 260px;
			overflow-y: scroll; }
		&-message {
			display: flex;
			align-items: flex-start;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } }
		&-name {
			font-size: 14px;
			color: #676767;
			margin-bottom: 4px; }
		&-text {
			font-size: 16px;
			font-weight: 500; }
		&-avatar {
			width: 40px;
			height: 40px;
			border-radius: 10px;
			margin-right: 17px; }
		&-form {
			display: flex;
			align-items: center;
			width: 100%;
			border-radius: 20px 0 0 20px;
			margin-top: 20px; }
		&-input {
			width: 100%;
			background-color: #fff;
			border-radius: 10px 0 0 10px;
			padding: 15px 25px;
			border: none;
			font-size: 16px;
			color: #232323; }
		&-btn {
			min-width: 180px;
			border-radius: 0 10px 10px 0; } } }

@media (max-width: 900px) {
	.cabinet {
		&__wrap {
			padding: 40px 0; }
		&__main {
			flex-direction: column;
			margin-top: 20px;
			&-avatar {
				width: 108px;
				height: 108px;
				margin-right: 15px; }
			&-discounts {
				display: none;
				margin: 15px 0;
				&--mobile {
					display: block; } } }
		&__card {
			margin-top: 10px;
			&-card {
				font-size: 0;
				span {
					font-size: 16px;
					margin-left: 0; } }
			&-btn {
				display: flex;
				align-items: center; } }
		&__master {
			&-block {
				flex-direction: column;
				margin-top: 20px; }
			&-img {
				margin-right: 0; }
			&-info {
				margin-top: 20px; }
			&-name {
				font-size: 18px;
				margin-bottom: 7px; }
			&-text {
				line-height: 1.8;
				margin-bottom: 7px; }
			&-btn {
				margin-top: 14px; } }
		&__chat {
			&-chat {
				padding: 20px;
				margin-top: 20px; }
			&-message {
				margin-bottom: 15px;
				&:last-child {
					margin-bottom: 0; } }
			&-avatar {
				display: none; }
			&-btn {
				min-width: 180px; } } } }

@media (max-width: 500px) {
	.cabinet {
		&__main {
			&-data {
				justify-content: space-between; } }
		&__chat-btn {
			position: relative;
			min-width: auto;
			font-size: 0;
			padding: 0;
			height: 48px;
			width: 58px;
			background: #fff url("../img/cabinet/send.png") no-repeat center;
			&:hover {
				background-color: #ffe749; } } } }
