.partners-certificate {
	&__carousel {
		margin-top: 40px;
		.owl-stage {
			display: flex; } }
	&__block {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		border-radius: 10px;
		border: 1px solid #e7e7e7;
		padding: 20px 15px;
		height: 100%;
		img {
			width: auto !important;
			max-width: 100%; } } }
