.contacts {
	&__block {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__address-block,
	&__contact-block {
		margin-top: 40px; }
	&__address-text,
	&__contact-link {
		font-size: 20px;
		font-weight: 600;
		color: #181818;
		line-height: 1.5; }
	&__address {
		max-width: 600px;
		&-link {
			display: block;
			margin-top: 10px;
			font-size: 16px;
			color: #d22a2a;
			text-decoration: underline;
			&:hover {
				color: #181818; } } }
	&__contact {
		display: flex;
		flex-wrap: wrap;
		&-block {
			margin-right: 60px;
			&:last-child {
				margin-right: 0; } }
		&-link:hover {
			color: #d22a2a; }
		&-text {
			margin-top: 5px;
			font-size: 16px;
			color: #181818; } } }

@media (max-width: 900px) {
	.contacts {
		&__address-block,
		&__contact-block {
			margin-top: 25px; }
		&__address-text {
			font-size: 16px; }
		&__contact-link {
			font-size: 20px; } } }
