.table {
	&__table {
		border-radius: 10px;
		overflow: hidden;
		margin-top: 40px;
		&--grey {
			.table__row {
				position: relative;
				font-weight: 600;
				border-bottom: 1px solid #f5f5f5;
				&:last-child {
					border-bottom: none; }
				&::before {
					content: '';
					position: absolute;
					top: 0;
					right: 200px;
					width: 1px;
					height: 100%;
					background-color: #f5f5f5; }
				&--head {
					background-color: #909090 !important;
					color: #fff;
					&::before {
						display: none; } } } } }
	&__row {
		display: flex;
		padding: 25px 30px;
		background-color: #fff;
		color: #232323;
		font-size: 16px;
		&:nth-child(odd) {
			background-color: #fcfcfc; }
		&--head {
			background-color: #ffe749 !important;
			font-weight: 600; } }
	&__column-1 {
		min-width: 80px;
		max-width: 80px;
		margin-right: 20px; }
	&__wrapper {
		display: flex;
		width: 100%;
		div {
			&:nth-child(1) {
				margin-right: 20px; }
			&:nth-child(2),
			&:nth-child(3) {
				min-width: 140px;
				max-width: 140px; }
			&:nth-child(2) {
				margin-left: auto; }
			&:nth-child(3) {
				margin-left: 30px; } } } }

@media (max-width: 800px) {
	.table {
		&__table {
			margin-top: 20px;
			&--grey .table__row::before {
					display: none; } }
		&__row {
			padding: 20px 10px;
			&--head {
				display: none; } }
		&__column-1 {
			min-width: 40px;
			max-width: 40px;
			margin-right: 10px; }
		&__wrapper {
			flex-wrap: wrap;
			div {
				&:nth-child(1) {
					width: 100%;
					margin-right: 0;
					margin-bottom: 10px; }
				&:nth-child(2),
				&:nth-child(3) {
					min-width: auto;
					max-width: none;
					font-weight: 600; }
				&:nth-child(2) {
					margin-left: 0;
					margin-right: 20px; }
				&:nth-child(3) {
					margin-left: 0; } } } } }
