body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Montserrat', sans-serif;
	line-height: 1.3;
	background: #ffffff; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none;
	text-decoration: none;
	margin: 0;
	padding: 0; }

ul,ol {
	list-style: none; }

iframe {
	width: 100%; }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

.global__bg {
	margin: 80px 0;
	&--grey {
		background-color: #f5f5f5;
		padding: 80px 0;
		margin-top: 0 !important;
		margin-bottom: 0 !important; }
	&--top {
		margin-top: 0 !important; } }

.text-semibold {
	font-weight: 600; }

.text-black {
	color: #181818; }

.global__btn {
	display: inline-block;
	background-color: #ffe749;
	padding: 15px 33px;
	border-radius: 10px;
	font-size: 16px;
	font-weight: 700;
	color: #000000;
	text-align: center;
	border: none;
	cursor: pointer;
	&:hover {
		background-color: #d22a2a;
		color: #fff; } }

.global__title {
	font-size: 32px;
	font-weight: 600;
	color: #181818;
	&--h1 {
		font-size: 36px;
		font-weight: 600;
		color: #181818; }
	&--small {
		font-weight: 600;
		font-size: 28px; } }

.owl-theme .owl-dots .owl-dot span {
	width: 18px;
	height: 18px;
	margin: 7px;
	background-color: #b3b3b3; }
.owl-theme .owl-dots .owl-dot.active span {
	background-color: #ffe749; }

@media (max-width: 900px) {
	.global__bg {
		margin: 40px 0;
		&--grey {
			padding: 40px 0;
			margin-top: 0 !important;
			margin-bottom: 0 !important; } }
	.global__title {
		font-size: 22px;
		span {
			display: none; }
		&--h1 {
			font-size: 26px; }
		&--small {
			font-size: 22px; } } }
