.articles-blog {
	&__block {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 40px; }
	&__btn {
		display: block;
		min-width: 240px;
		margin: auto;
		margin-top: 20px; }
	&__top-text {
		font-size: 16px;
		line-height: 1.8;
		margin-top: 20px; }
	&__link {
		position: relative;
		display: flex;
		width: calc(33% - 15px);
		margin-bottom: 30px;
		border-radius: 10px;
		overflow: hidden;
		img {
			width: 100%; }
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 100%;
			background: linear-gradient(rgba(255,255,255,0), rgba(0,0,0,0.7));
			opacity: 0.5; }
		&:hover::before {
			background: linear-gradient(rgba(255,255,255,0), rgba(0,0,0,1)); }
		&--big {
			width: calc(66.5% - 7.5px);
			margin-bottom: 30px; } }
	&__duble-link {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: space-between;
		width: calc(33% - 15px);
		.articles-blog__link {
			width: 100%; } }
	&__img {
		max-width: 100%; }
	&__wrapper {
		position: absolute;
		bottom: 15px;
		left: 25px;
		max-width: calc(100% - 50px); }
	&__text {
		font-size: 20px;
		font-weight: 600;
		line-height: 1.6;
		color: #fff;
		margin-top: 5px;
		&-title {
			color: #fff;
			font-size: 16px; } } }

@media (max-width: 1260px) {
	.articles-blog__text {
		font-size: calc(0.3rem + 1.10vw); } }

@media (max-width: 900px) {
	.articles-blog {
		&__block {
			margin-top: 20px; }
		&__link {
			width: calc(50% - 5px);
			margin-bottom: 10px;
			&--big {
				width: 100%;
				margin-bottom: 10px; } }
		&__duble-link {
			flex-direction: row;
			width: 100%;
			.articles-blog__link {
				width: calc(50% - 5px); } }
		&__text {
			font-size: 16px;
			&-title {
				font-size: 15px; } }
		&__wrapper {
			left: 18px;
			max-width: calc(100% - 36px); } } }


@media (max-width: 600px) {
	.articles-blog {
		&__link {
			width: 100%; }
		&__duble-link {
			flex-direction: column;
			.articles-blog__link {
				width: 100%; } } } }
