.nav {
	&__wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid #f5f5f5;
		position: relative;
		&--top {
			padding: 5px 0; }
		&--bottom {
			padding: 20px 0; } }
	&__btn-menu {
		display: none;
		position: relative;
		width: 33px;
		height: 33px;
		background-color: transparent;
		border: 1px solid #f5f5f5;
		border-radius: 4px;
		cursor: pointer;
		span,
		span::before,
		span::after {
			position: absolute;
			top: 0;
			left: 0;
			content: '';
			width: 17px;
			height: 2px;
			background-color: #4f4f4f; }
		span {
			top: calc(50% - 1px);
			left: calc(50% - 8px); }
		span::before {
			transform: translateY(-6px); }
		span::after {
			transform: translateY(6px); }
		&--active {
			background-color: #f5f5f5;
			border-radius: 4px 4px 0 0;
			span,
			span::before,
			span::after {
				background-color: red; } } }
	&__mobil-tel {
		display: none;
		font-size: 18px;
		font-weight: 700;
		color: #181818; }
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		position: relative;
		margin-right: 10px;
		&:last-child {
			margin-right: 0; }
		&:hover .nav__link {
			background-color: #f5f5f5;
			color: #d22a2a; }
		&:hover .nav__sub-list {
			display: flex; }
		&:hover .nav__link--arrow {
			background-image: url(../img/icon-arrow-up-red.png); }
		&:hover .nav__link--enter {
			background-image: url(../img/icon-enter-active.png); } }
	&__link {
		display: flex;
		align-items: center;
		padding: 15px;
		border-radius: 8px;
		font-size: 15px;
		color: #6f6f6f;
		&--arrow,
		&--enter {
			background-repeat: no-repeat; }
		&--arrow {
			background-image: url(../img/icon-arrow-down-grey.png);
			background-position: calc(100% - 15px) center;
			padding-right: 34px;
			border-radius: 8px 8px 0 0; }
		&--enter {
			background-image: url(../img/icon-enter.png);
			background-position: 10px center;
			padding-left: 34px; }
		&--main {
			background-image: url(../img/icon-arrow-down-black.png);
			background-position: calc(100% - 25px) center;
			font-size: 16px;
			font-weight: 600;
			padding-left: 22px;
			padding-right: 52px;
			border-radius: 8px 8px 0 0; } }
	&__sub {
		&-list {
			display: none;
			position: absolute;
			top: 100%;
			right: 0;
			flex-direction: column;
			background-color: #f5f5f5;
			padding: 5px 15px;
			padding-left: 30px;
			border-radius: 8px 0 8px 8px;
			z-index: 100;
			&--main {
				right: auto;
				left: 0;
				padding: 5px 15px;
				padding-right: 40px;
				.nav__sub-link {
					font-size: 16px;
					justify-content: flex-start;
					text-align: left; } } }
		&-link {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			padding: 5px 0;
			font-size: 15px;
			color: #000000;
			text-align: right;
			white-space: nowrap;
			&:hover,
			&:focus {
				color: #d22a2a; } } }
	&__tel {
		display: flex;
		flex-direction: column;
		text-align: right;
		font-size: 16px;
		color: #8d8d8d;
		&-link {
			font-size: 20px;
			font-weight: 700;
			color: #181818; } }
	&__mobile-list {
		display: none; } }

@media (max-width: 1200px) {
	.nav__btn-call {
		display: none; } }

@media (max-width: 1100px) {
	.nav__link--enter {
		font-size: 0;
		background-position: center;
		padding: 15px; } }

@media (max-width: 1000px) {
	.nav {
		&__list--main {
			margin-left: 30px;
			margin-right: auto; }
		&__tel {
			display: none; } } }

@media (max-width: 900px) {
	.nav {
		&__link {
			display: none;
			&--enter {
				display: flex; } }
		&__btn-menu,
		&__mobil-tel {
			display: block; }
		&__mobile {
			&-list--active {
				position: absolute;
				top: calc(100% - 27px);
				right: 0;
				display: flex;
				flex-direction: column;
				width: 100%;
				padding: 0 20px;
				padding-bottom: 10px;
				background-color: #f5f5f5;
				border-radius: 4px 0 4px 4px;
				z-index: 100; }
			&-item {
				display: flex;
				&--main {
					border-bottom: 1px solid #dbdbdb; } }
			&-link {
				width: 100%;
				padding: 10px 0;
				font-size: 16px;
				color: #181818;
				&:hover {
					color: #d22a2a; }
				&--main {
					padding: 20px 0;
					font-weight: 600;
					border-bottom: 1px solid #fff; } } } } }
