.article-sale {
	&__block {
		background: red url(../img/brand-category/bg-article.jpg) no-repeat center;
		background-size: cover;
		padding: 35px 50px;
		border-radius: 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 200px;
		&--img {
			background-image: url(../img/usluga/bg.jpg); } }
	&__right {
		color: #fff;
		max-width: 490px;
		min-width: 490px;
		margin-right: 40px; }
	&__title {
		margin-bottom: 10px; }
	&__text {
		font-size: 20px;
		line-height: 1.5;
		max-width: 480px; }
	&__form {
		display: flex;
		align-items: center;
		width: 100%; }
	&__input {
		background-color: #fff;
		border-radius: 10px 0 0 10px;
		padding: 15px 25px;
		border: none;
		font-size: 16px;
		width: 100%;
		color: #232323; }
	&__btn {
		border-radius: 0 10px 10px 0; } }

@media (max-width: 1050px) {
	.article-sale {
		overflow: hidden;
		&__wrap {
			margin-left: -20px;
			margin-right: -20px; }
		&__block {
			flex-direction: column;
			padding: 30px 35px; }
		&__right {
			min-width: auto;
			margin-right: 0;
			text-align: center; }
		&__form {
			margin-top: 20px; } } }

@media (max-width: 900px) {
	.article-sale {
		&__text {
			font-size: 16px; } } }

@media (max-width: 500px) {
	.article-sale {
		&__form {
			flex-direction: column; }
		&__input {
			border-radius: 10px;
			min-width: 290px;
			margin-bottom: 20px; }
		&__btn {
			min-width: 290px;
			border-radius: 10px; } } }
